@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans");

/* Add to your main CSS file (e.g., index.css) */

.toplevel_page_uavc #wpcontent,

:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-steps span.absolute {
	font-size: 13px;
}
:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-steps .ring-border-subtle {
	--tw-ring-color: #64748B;
}
:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-steps .border-border-subtle {
	border-color: #64748B;
}
:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-steps .min-w-10.border-y {
	min-width: 9rem;
	border-bottom-width: 0.5px;
}

@media (min-width: 769px) {
	.uavc-sticky-outer-wrapper {
		width: 23%;
		border-right: 1px solid #e5e7eb;
	}
	.uavc-sticky-sidebar {
		position: sticky;
		top: 0;
	}
	.uavc-settings-page:before {
		animation-fill-mode: both;
		background: #fff;
		content: " ";
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		transform-origin: right;
		border-right: 1px solid #e5e7eb;
		width: 30%;
	}
	.uavc-nav-menu {
		border-bottom: 1px solid #e5e7eb;
	}
}


#uavc-settings-app .loader {
	border: 4px solid #f3f3f3;
	border-radius: 50%;
	border-top: 4px solid #3498db;
	width: 40px;
	height: 40px;
	animation: spin 2s linear infinite;
}
.uavc-custom-scrollbar::-webkit-scrollbar {
	width: 4px;
}  
/* Track */
.uavc-custom-scrollbar::-webkit-scrollbar-track {
	background: #f1f1f1; 
}
   
/* Handle */
.uavc-custom-scrollbar::-webkit-scrollbar-thumb {
	background: #888; 
}
  
/* Handle on hover */
.uavc-custom-scrollbar::-webkit-scrollbar-thumb:hover {
	background: #555; 
}

.uavc-subheading h2 {
	font-size: 1.1rem !important;
}
.uavc-subheading .text-text-secondary {
	font-size: 0.98rem !important;
}
.uavc-loader-icon {
	top: -15%;
}
.uavc-remove-ring.uavc-span span {
	padding-left: 0px;
}
#uavc-settings-app .gap-2 {
	gap: 1.2rem;
}

#uavc-settings-app .uavc-header-title h6 {
	color: #858484;
	font-size: 14px;
}

.uavc-remove-ring input[type="checkbox"],
.uavc-remove-ring a,
.uavc-remove-ring {
	outline: none !important;
	--tw-ring-inset: auto;
	box-shadow: none !important;
	-webkit-tap-highlight-color: transparent;
}

.uavc-demo-link:focus {
	outline: none !important;
	box-shadow: none !important;
	border: none !important;
}
.uavc-settings-page h2 {
	margin-bottom: 0.3rem !important;
}
:is(#uavc-settings-app) .uavc-remove-ring svg.lucide-arrow-right {
	width: 1rem;
	height: 1rem;
}
.uavc-popup-button,
.uavc-popup-button svg {
	padding: 0;
}
.uavc-popup-button span {
	padding-left: 0;
}
.uavc-link-color,
.uavc-link-color span {
	color: rgb(96, 5, 255);
}
#uavc-settings-app .uavc-header-title h6 {
	color: #858484;
	font-size: 14px;
}
.uavc-settings-content-wrapper {
	padding: 15px 25px;
}
button.uavc-plugin-activated {
	color: #16a34a;
}
button.uavc-bulk-action-button {
	width: 150px;
}
.uavc-subtitle h2 {
	font-size: 0.9rem;
}
.uavc-65-width {
	width: 65%;
}
.uavc-35-width {
	width: 35%;
}
.uavc-78-width {
	width: 78%;
}
.uavc-88-width {
	width: 88%;
}
#uavc-settings-app .subject-cell {
	max-width: 200px; /* Adjust this width based on your layout */
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}
.uavc-tooltip-wrap {
	width: 100%;
    text-align: right;
}
.toplevel_page_uavc #wpcontent {
	padding-left: 0;
}
div#uavc-settings-app {
	font-size: 15px;
	font-weight: 500;
	line-height: normal;
}

input[type="radio"].uavc-radio-field:checked::before {
	content: "";
    border-radius: 50%;
    width: 0.5rem;
    height: 0.5rem;
    margin: 0.1875rem;
    background-color: #6005FF;
    line-height: 1.14285714;
}
input[type="radio"].uavc-radio-field:focus {
	border-color: #6005ff;
	box-shadow: 0 0 0 1px #6005ff;
}
.uavc-compare-section h6 {
	font-size: 0.9rem !important;
	font-weight: 500 !important;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}
