	#toplevel_page_uavc .container {
		padding: 0 1rem;
	}

	#toplevel_page_uavc .fixed {
		justify-content: flex-start !important;
	}

	#toplevel_page_uavc .bg-white {
		width: 100% !important;
		height: 100% !important;
		border-radius: 0 !important;
	}
}
