}
@media screen and (max-width: 782px) {
	:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-display-flex {
		display: block;
	}
	.uavc-radio-option {
		width: 1rem !important;
		height: 1rem !important;
	}
	.uavc-radio-option:checked:before {
		width: 0.5rem !important;
		height: 0.5rem !important;
		margin: 0.18rem !important;
	}
	:is(#uavc-settings-app, [data-floating-ui-portal]) .uavc-steps,
	#uavc-settings-app img {
		display: none;
	}
	.uavc-onboarding-customize {
		margin-left: 20px;
    	margin-right: 20px;
	}
	.uavc-min-700 {
		min-width: unset;
	}
	.uavc-onboarding-customize .uavc-custom-scrollbar {
		overflow: scroll;
		max-width: fit-content;
	}
	.uavc-onboarding-bottom {
		display: grid !important;
		max-width: fit-content;
	}
}

.uavc-iframe-wrapper {
	width: 100%;
	max-width: 100%;
	display: flex;
	justify-content: flex-end;
}

.uavc-responsive-iframe {
	width: 280px;
	height: 160px;
	border-radius: 8px;
	border: none;
}

/* On large screens, make iframe scale responsively */
@media (min-width: 1024px) {
	.uavc-iframe-wrapper {
		width: 100%;
		max-width: 480px; /* or another max width that fits well */
		height: auto;
		aspect-ratio: 16 / 9;
	}
}

/* Hide scrollbar - works in most modern browsers */
.uavc-hide-scrollbar {
	scrollbar-width: none;       /* Firefox */
	-ms-overflow-style: none;    /* Internet Explorer 10+ */
  }
  
  .uavc-hide-scrollbar::-webkit-scrollbar {
	display: none;               /* Safari and Chrome */
  }
  

